.title {
    font-size: 50px;
    text-align: center;
}
#search-form {
    font: inherit;
    padding: 8px;
    /* max-width: 320px; */
    /* min-width: 500px; */
    margin-bottom: 16px;
    text-align: center;
    /* margin-right: auto; */
    /* margin-left: auto; */
}

.header {
    display: block;
    margin: 0 auto;
    width: 100%;
    padding: 45px 20px;
    height: 230px;
    color: #FFF;
    background-color: #2a2a2a;
    background-image: linear-gradient(90deg, rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)),
    url(/images/header-background-image.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
};

.nav__container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 37px;

    @media screen and (max-width: 767px) {
        margin-bottom: 45px;
    }
}

.logo{
    display: flex;
    align-self: flex-end;

    @media screen and (max-width: 767px) {
        margin-right: 112px;
    }
}

.header__logo {
    width: 20px;
    height: 20px;
    align-self: center;
    stroke: #818181;
    transition-property: stroke;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.logo__animation {
    width: 24px;
    height: 24px;

}

.header__title {
    font-size: 30px;
    line-height: 1.72;
    font-weight: 500;
    color: #FFF;

    @media screen and (max-width: 767px) {
        display: none;
    }
};

.navigation {
    display: block;
}

.menu {
    display: flex;
}

.navigation__item {
    position: relative;
}

.navigation__item:not(:last-child) {
    margin-right: 39px;
}

.navigation__link {
    font-size: 12px;
    line-height: 1.16;
    color: #fff;
    text-transform: uppercase;
}

.search {
    display: flex;
    position: relative;
    width: 268px;
    margin: 0 auto 22px;
    justify-content: center;
}

.search__input {
    width: 280px;
    height: 30px;
    padding: 4px 0;
    border: none;
    background-color: transparent;
    color: #FFF;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.17;
    border-bottom: 0.5px solid #fff;
    transition-property: border-bottom;
    margin: 0;
}

.search__input::placeholder {
    color: #fff;
}

.search__button {
    position: absolute;
    right: 0;
    width: 20px;
    height: 20px;
    border: none;
    outline: none;
    background-color: transparent;
}

.search-icon{
    width: 12px;
    height: 12px;
}

.search__button-icon {
    position: absolute;
    right: 0;
    bottom: 2px;
    stroke: #fff;
    transition-property: stroke;
}
